.burger-menu {
  width: 24px;
  height: 16px;
  z-index: 40;
  font-size: 0;
  display: none;
  position: relative;
}

.burger-menu:before, .burger-menu:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #fcfcfc;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  left: 0;
}

.burger-menu:before {
  top: 0;
}

.burger-menu:after {
  bottom: 0;
}

.burger-menu .burger-menu__item {
  width: 100%;
  height: 3px;
  background-color: #fcfcfc;
  position: absolute;
  top: 41%;
  left: 0;
}

.burger-menu.is-open:before {
  background-color: #fcfcfc;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  top: 8px;
  transform: rotate(-45deg);
}

.burger-menu.is-open:after {
  background-color: #fcfcfc;
  bottom: 5px;
  transform: rotate(45deg);
}

.burger-menu.is-open .burger-menu__item {
  transform: scale(0);
}

@media (min-width: 200px) {
  .burger-menu {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    display: block;
  }
}

